@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Nanum Gothic', sans-serif;
  font-size: medium;
  padding: 0 !important;

  /* padding: 20px 60px; */

}

a {
  text-decoration: none;
  color: black;
}

*{
  box-sizing: border-box;

}
.parallax{position:relative;width:100vw;height:90vh;max-width:100%;display:flex;align-items:center;justify-content:center;background-repeat:no-repeat;overflow-x:hidden;background-attachment:fixed;background-size:cover;background-image:linear-gradient(to bottom, rgba(0,0,0,0.3) 0%, rgba(0,0,0,0.3) 100%),url(/static/media/a1.b7f6f04b.jpg)}.parallax .text-container{height:200px;width:500px;padding:10px 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#554937;opacity:0.7;position:absolute}.parallax .text-container .title{text-align:center;color:white;font-family:'Raleway', sans-serif;font-size:40px}.homepage{padding:40px 60px;display:flex;flex-direction:column;align-items:center}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:center}.menu-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:0.5px solid black;background-color:white;opacity:0.7;position:absolute}.menu-item .content .title{font-weight:bold;margin:0 6px 0;font-size:22px;color:#534939}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.collection-item{width:22vw;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center}.collection-item .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{display:flex}.collection-item .collection-footer{width:100%;height:10%;background-color:black;display:flex;justify-content:space-between;font-size:15px;align-items:center}.collection-item .collection-footer .name{width:90%;color:white;font-weight:600}.collection-item .collection-footer::before{content:"\00a0 \00a0 \00a0"}.collection-item .collection-footer .price{width:15%;color:white}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:20px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}

.shop-page{padding:20px 60px}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:25px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px;padding:20px 60px}.header .logo-container{height:100%;width:70px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end;padding:20px 60px;font-weight:bold}.header .options .option{padding:10px 15px;cursor:pointer}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5;overflow-y:scroll}.cart-dropdown .empty-space{margin:80px auto;display:table}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.sign-in-and-sign-up{width:1000px;display:flex;justify-content:space-between;margin:50px 200px}

.sign-in{width:30vw;display:flex;flex-direction:column}.sign-in .title{margin:10px 0}.sign-in .buttons{display:flex;justify-content:space-between}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-up{display:flex;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0;padding:20px 60px}.checkout-page .checkout-header{width:100%;padding:10px 10px;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{font-size:17px;text-transform:lowercase;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{text-transform:lowercase;margin-top:30px;margin-left:auto;font-size:17px}.checkout-page button{margin-left:auto;margin-top:50px}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0px;font-size:17px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px;height:40%}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{padding-left:20px;width:23%}.checkout-item .quantity{display:flex;padding-left:20px}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

