.checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgrey;
    padding: 15px 0px;
    font-size: 17px;
    align-items: center;
    // padding: 20px 10px;
  
    .image-container {
      width: 23%;
      padding-right: 15px;
      height: 40%;
  
      img {
        width: 100%;
        height: 100%;
      }
    }
    .name,
    .quantity,
    .price {
      padding-left: 20px;
      width: 23%;
    }
  
    .quantity {
      display: flex;
      padding-left: 20px;
      .arrow{
        cursor: pointer;
      }
      .value{margin: 0 10px}
    }
  
    .remove-button {
      padding-left: 12px;
      cursor: pointer;
    }
  }