.collection-item {
    width: 22vw;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
  
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
    //   margin-bottom: 5px;
    }

    .custom-button{
      width: 80%;
      opacity: 0.7;
      position: absolute;
      top: 255px;
      display: none;
     
    }

    &:hover{
        .image{
          opacity: 0.8;
        }
        .custom-button{
          display: flex;
        }
    }
  
    .collection-footer {
       
      width: 100%;
      height: 10%;
      background-color: black;
      display: flex;
      justify-content: space-between;
      font-size: 15px;
      align-items: center;

  
      .name {
        width: 90%;
        color: white;
        font-weight: 600;
      }

      &::before{
          content: "\00a0 \00a0 \00a0";
      }
  
      .price {
        width: 15%;
        color: white;

      }

   
    }
}